@import url(https://fonts.googleapis.com/css2?family=Baloo+Da+2:wght@400;500;600;700;800&family=Muli:ital,wght@0,200;0,300;0,400;1,200;1,300;1,400&display=swap);
body, * {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  width: 100%;
}


.container {
  width: 100%;
  height: 100%;
  background-color: #77c9c9;
  display: flex;
  justify-content: center;
  align-items: center;
}

.app-wrapper {
  width: 90%;
  max-width: 1500px;
  margin: 1rem;
  background-color: #2c2c2d;
  color: #eee;
  border-radius: 0.5rem;
  box-shadow: 0 1rem 5rem rgba(0, 0, 0, 0.7);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.header {
  width: 100%;
  text-align: center;
  margin: 1rem auto;
}

.header h1 {
  font-family: "Muli", serif;
  font-size: 3rem;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  color: #f46036;
  text-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.4);
}

.balance {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-top: 1rem;
}

.balance h2 {
  font-family: "Baloo Da 2", serif;
  font-size: 1.2rem;
  font-weight: 300;
  text-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.4);
}

.balance h3 {
  font-family: "Baloo Da 2", serif;
  font-size: 1.2rem;
  font-weight: 400;
  text-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.4);
}

.income-expense {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  font-family: "Muli", serif;
  text-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.4);
  text-align: center;
  width: 100%;
  padding: 1rem;
}

.plus {
  color: #77c987;
}

.minus {
  color: #ff716e;
}

.income-expense h3 {
  font-size: 1.2rem;
  font-weight: 300;
}

.income-expense p {
  font-size: 1.2rem;
  font-weight: 700;
  text-align: center;
}

.form-wrapper {
  width: 100%;
}

.input-group {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 1.5rem auto;
}

/* .income {
}

.expense {
} */

.input-group input:not([type="submit"]) {
  width: 80%;
  border: none;
  background: transparent;
  border-bottom: 0.2rem solid #777;
  padding: 0.5rem 0.5rem 0.5rem 0;
  font-family: "Baloo Da 2", serif;
  font-size: 1.2rem;
  color: #eee;
  outline: none;
  transition: border-bottom 0.4s;
}

.income input:not([type="submit"]):focus {
  border-bottom: 0.2rem solid #77c987;
}

.expense input:not([type="submit"]):focus {
  border-bottom: 0.2rem solid #ff716e;
}

.input-group input[type="submit"] {
  width: 50%;
  padding: 0.7rem;
  border: none;
  outline: none;
  border-radius: 0.7rem;
  font-family: "Muli", serif;
  font-size: 1.2rem;
  font-weight: 700;
  letter-spacing: 0.1rem;
  color: #eee;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2);
  cursor: pointer;
  margin-top: 1rem;
  text-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.4);
}

.input-group input[type="submit"]:active {
  transform: translateY(0.3rem);
}

.income input[type="submit"] {
  background-color: #77c987;
}

.expense input[type="submit"] {
  background-color: #ff716e;
}

input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

.transactions {
  margin-top: 3rem;
}

.transactions h2 {
  font-family: "Baloo da 2", serif;
  font-size: 2rem;
  letter-spacing: 0.1rem;
  font-weight: 300;
  margin-bottom: 2rem;
  text-align: center;
}

.transaction-list {
  width: 95%;
  margin-left: auto;
  margin-right: auto;
  max-height: 300px;
  overflow: hidden;
  overflow-y: auto;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  border-radius: 10px;
  background-color: #aaa;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #444;
  border-radius: 10px;
}

.transaction {
  list-style: none;
  font-family: "Baloo da 2", serif;
  font-size: 1rem;
  letter-spacing: 0.1rem;
  margin-bottom: 1.5rem;
  color: #fff;
  padding: 0.4rem 0.4rem 0.4rem 0.7rem;
  border-radius: 0.3rem;
  display: grid;
  grid-template-columns: 3fr 1fr;
  grid-template-rows: repeat(2, 1fr);
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2);
}

.transactions-income .transaction {
  background-color: #5a9b67;
}

.transactions-expense .transaction {
  background-color: #cf5856;
}

.transactions-expense {
  margin-bottom: 4rem;
}

.transaction-text {
  grid-row: 1 / 2;
  grid-column: 1 / 2;
}

.transaction-amount {
  grid-row: 2 / 3;
  grid-column: 1 / 2;
}

.delete-btn {
  grid-row: 1 / -1;
  grid-column: 2 / 3;
  border: none;
  background-color: transparent;
  font-size: 2rem;
  margin: 0 1rem;
  color: #d4403e;
  text-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.4);
  cursor: pointer;
  outline: none;
}

@media (min-width: 768px) {
  .balance h2 {
    font-size: 1.5rem;
  }

  .balance h3 {
    font-size: 1.5rem;
  }

  .income-expense h3 {
    font-size: 1.5rem;
  }

  .income-expense p {
    font-size: 1.5rem;
  }

  .input-group input:not([type="submit"]) {
    max-width: 600px;
  }

  .input-group input[type="submit"] {
    max-width: 300px;
  }

  .transactions {
    width: 80%;
  }

  .transactions h2 {
    text-align: left;
  }
}

@media (min-width: 1200px) {
  .container {
    height: 100vh;
  }
  .app-wrapper {
    display: grid;
    grid-template-columns: repeat(2, 1fr) 100px repeat(2, 1fr);
    grid-template-rows: 100px repeat(2, 1fr) 2fr;
    justify-content: center;
    align-items: center;
    
  }

  .header {
    grid-column: 2 / 5;
    grid-row: 1 / 2;
    justify-self: center;
   align-self: center;
   margin-top: 2rem;
  }

  .header h1 {
    font-size: 4.5rem;
  }

  .balance {
    max-width: 1200px;
    grid-column: 2 / 5;
    grid-row: 2 / 3;
    justify-self: center;
   align-self: center;
  }

  .balance h2 {
    font-size: 2rem;
  }

  .balance h3 {
    font-size: 2rem;
  }

  .income-expense h3 {
    font-size: 2rem;
  }

  .income-expense p {
    font-size: 2rem;
  }

  .form-wrapper {
    grid-column: 1 / -1;
    grid-row: 3 / 4;
    justify-self: center;
    align-self: center;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
  }
  .input-group input:not([type="submit"]) {
    width: 100%;
  }

  .transactions-income {
   grid-column: 1 / 3;
   grid-row: 4 / -1;
   justify-self: center;
   align-self: start;
  }
  .transactions-expense {
    grid-column: 4 / -1;
    grid-row: 4 / -1;
    justify-self: center;
    align-self: start;
  }
  
}

